const initialState = {
    player_name : "",
    room_name : "",
    players: []
}


const reducer = (state = initialState, action) => {
    switch (action.type) {
        case "ADD_PLAYER_NAME":
            // console.log(action.player_name)
            return{
                ...state,
                player_name : action.player_name,
            }
        case "ADD_ROOM_NAME":
            // console.log(action.room_name)
            return{
                ...state,
                room_name : action.room_name
            }
        case "ADD_TOTAL_PLAYERS":
            return{
                ...state,
                players : action.players
            }
        case "LEAVE_PLAYERS":
            return{
                ...state,
                players : action.players
            }


        default:
            return{
                ...state
            }            
    }
}


export default reducer;