export const add_player_name = (player_name) => {
    return{
        type: "ADD_PLAYER_NAME",
        player_name : player_name
    }
}


export const add_room_name = (room_name) => {
    return{
        type: "ADD_ROOM_NAME",
        room_name : room_name
    }
}

export const add_total_players = (players) => {
    return{
        type: "ADD_TOTAL_PLAYERS",
        players : players
    }
}

export const leave_players = (players) => {
    return{
        type: "LEAVE_PLAYERS",
        players : players
    }
    
}