import React, { Component } from "react";
import {connect} from "react-redux";
import Celebration from "../animation/celebration/celebration";
import audio1 from '../../assets/audio/cheering.mp3';

import { socket } from '../home/home';
import Rules from "../Rules/rules";

import "./game.css"


class Game extends Component {
    state = {
        chit:[],
        hostNumber : [],
        king : "",
        showModal : false,
        showClaim : false,
        claimer : "",
        room_id : "",
        start :  false,
        ShowRules : false,
    }


    componentDidMount(){

        if(this.state.showModal){
            this.playAudio()
        }

        
        socket.on('king', (data) => {
            console.log("im king",data)
            this.setState({...this.state, king : data.king})
        })

        socket.on('chits', (data) => {
            this.setState({...this.state, chit : data.chit})
        })

        socket.on('claim', (data) => {
            console.log("claim ->", data.msg)
            if(data.msg === "YES"){
                this.setState({...this.state, showClaim : false, claimer: data.claimer})
                this.openModal()

            }
            else{
                this.setState({...this.state, showClaim : true, claimer: data.claimer})
            }
        })

        socket.on('playAgain', (data) => {
            this.setState({...this.state, hostNumber : [], start: false})
            socket.emit('chits')
        })


        socket.on('start', (data) => {
            // console.log("start", data.msg)
            this.setState({...this.state, hostNumber : [...this.state.hostNumber, data.msg], start : true})
        })

    }


    onNextNumber = () => {
        console.log("next_number")
        if(this.state.king === this.props.player_name){
            socket.emit('start', {'room' : this.props.room_name})

        }
        else{
            console.log("you are not host")
        }

    }

    playAudio = () => {
        let audio = new Audio(audio1)
        audio.play()
      };

    openModal = () =>{
        this.setState({...this.state, showModal : !this.state.showModal})
        // console.log(this.state.showModal)
    }

    getChit = () => {
        socket.emit('chits')
        document.getElementById('chit_button').disabled = 'true'
    }

    playAgain = () => {
        this.setState({...this.state, hostNumber: [], })
        socket.emit('playAgain', {'room': this.props.room_name})
        this.openModal()
        let z = document.getElementsByClassName("chit_numbers")

        for(let i = 0; i < z.length;i++){
            z[i].style.backgroundColor = 'white'
            z[i].style.textDecoration = 'none'
        }       

    }

    claim = () => {
        socket.emit('claim', {'room' : this.props.room_name, 'number_list' : this.state.chit, 'claimer': this.props.player_name})
    }

    changeColor = (e) => {
        let id = e.target.id
        if (document.getElementById(id).style.backgroundColor === 'white'){
            document.getElementById(id).style.textDecoration = "line-through"
            document.getElementById(id).style.backgroundColor = "Red"   
        }
        else{
            document.getElementById(id).style.backgroundColor = 'white'
            document.getElementById(id).style.textDecoration = "none"

        }
    }

    ShowRules = (e) => {
        console.log("inside show rules")
        this.setState({...this.state, ShowRules: e})
    }


    render(){
        return(
            <div className = "game">   
                <div className = "Rules">
                    <button onClick = {() => this.ShowRules(true)}> Show Rules</button>
                    <Rules show={this.state.ShowRules} openfunc = {this.ShowRules}/>
                </div>
                <div className = "number-generator">   
                    <button className = "number-generator-button" onClick = {this.onNextNumber}>{this.state.hostNumber[this.state.hostNumber.length - 1]}
                    </button>
                    
                </div>
                
                <div className = "tambola"> 
                    {this.state.showModal ?  
                        <div id="myModal" className="modal">
                            <div className="modal-content">
                                <div>        
                                    <div>
                                        
                                        <audio style={{width : "150px"}} className = "audio-element" controls>
                                            <source src= {audio1} type="audio/mpeg" />
                                        </audio>
                                        <Celebration />   
                                    </div> 
                                    <div>
                                        <h1>CONGRATS</h1>
                                        <p>{this.state.claimer} is the winner</p>
                                        <button style = {{backgroundColor: "#523160", color : "white", padding: "5px"}} onClick = {this.playAgain}>play again</button>
                                    </div>      
                                </div>
                            </div>
                        </div> : ""
                    }

                    

                   

                    {this.state.showClaim ? <p>{this.state.claimer} claims: he is BOOGY <p> <b>Warning : </b>Claim only when all numbers are cut</p></p>: null}

                    <div><button className = "claim_button" onClick = {this.claim}>CLAIM</button></div>
                    <p>
                    {this.state.start  ? <p style = {{color : "yellow"}}>Match has been Started</p> : null}
                    </p>
                    
                
                    <p><button onClick = {this.getChit} id = "chit_button">chit</button></p>

                    {/* {this.state.king ? <p><button onClick = {this.onStart}>Start</button></p> : ""} */}
                    <div className = "chit">
                        <div>
                            {this.state.chit.map(num => {
                            // console.log(this.state.chit.indexOf(num))
                                return(
                                <button id = {"id" + this.state.chit.indexOf(num)} className = "chit_numbers" onClick = {(e) => this.changeColor(e)}>{num}</button>
                                )})}
                        </div>
                   
                    </div>
                </div>
            </div>
        )
    }
}


const mapStateToProps = (state) => {
    return {
        player_name : state.player_name,
        room_name : state.room_name,
        players : state.players
    }
}

export default connect(mapStateToProps)(Game);