import React, { Component } from "react";

import "./rules.css"

class Rules extends Component {



    close = () => {
        this.props.openfunc(false)
    }

    render() {
        if (!this.props.show) {
            return null;
        }
        return (
            <div id="myModal" className="modal">
                <div className="modal-content">
                    {/* <span className="close">&times;</span> */}
                    <p><h2 style={{color: "black"}}>Instructions/Rules</h2></p>
                    <ul>
                        <li>Minimum two players are Required to play this game.</li>
                        <li>The player who has created the room, is considered as HOST.</li>
                        <li>Only HOST draw number by pressing big circular orange button, number can be seen on that button only,there will be no chance of checking past numbers, so please stay focused during the game.</li>
                        <li>Click on chit button, to get your chit, after one game you are allowed to change chit only once.</li>
                        <li>If all the numbers in chit are dabbed/cut then press on the claim button.</li>
                        <li>The player who got his all numbers dabbed/cut is the winner there is no second or third.
                        </li>
                        <li>If your claimed winning pattern is wrong, it will be called BOOGY and you get warning, if you try many times then you cannot continue the game with the same ticket or get kicked out of the game.</li>
                        <li>If a room owner/ HOST leave the game, then game is over and there will be no winner, you have to create a new room or join another room, you can only chat in lobby.</li>
                        <button style = {{backgroundColor: "#DC143C"}} onClick= {this.close}>Close</button>
                    </ul>
                </div>

            </div>

        );
        }


            
        
   
}

export default Rules;